export default class CustomPalette {
  constructor(bpmnFactory, create, elementFactory, palette, translate) {
    this.bpmnFactory = bpmnFactory;
    this.create = create;
    this.elementFactory = elementFactory;
    this.translate = translate;

    palette.registerProvider(this);
  }

  getPaletteEntries() {
    // getPaletteEntries(element) {
    const { bpmnFactory, create, elementFactory, translate } = this;

    function createStart(name, type) {
      return function(event) {
        const businessObject = bpmnFactory.create('bpmn:StartEvent', {
          name: name,
          'activiti:nodeType': type,
        });
        const shape = elementFactory.createShape({
          type: 'bpmn:StartEvent',
          businessObject,
        });
        // console.log(elementFactory.createShape) // 
        create.start(event, shape);
      };
    }
    // function createFlow(name) {
    //   return function(event) {
    //     const businessObject = bpmnFactory.create('bpmn:SequenceFlow', {
    //       name: name,
    //     });
    //     const shape = elementFactory.createShape({
    //       type: 'bpmn:SequenceFlow',
    //       businessObject,
    //     });
    //     create.start(event, shape);
    //   };
    // }

    function createEnd(name, type) {
      return function(event) {
        const businessObject = bpmnFactory.create('bpmn:EndEvent', {
          name: name,
          'activiti:nodeType': type,
        });
        const shape = elementFactory.createShape({
          type: 'bpmn:EndEvent',
          businessObject,
        });
        create.start(event, shape);
      };
    }

    function createTask(name, type) {
      return function(event) {
        const businessObject = bpmnFactory.create('bpmn:UserTask', {
          name: name,
          'activiti:nodeType': type,
          'activiti:forEnd': false,
          'activiti:forNum': '0',
        });
        const shape = elementFactory.createShape({
          type: 'bpmn:UserTask',
          businessObject,
        });
        create.start(event, shape);
      };
    }

    function createSpecalTask(name, obj, type) {
      return function(event) {
        const businessObject = bpmnFactory.create('bpmn:UserTask', {
          name: name,
          'activiti:nodeType': type,
          'activiti:forEnd': false,
          'activiti:forNum': '0',
        });
        for (let i in obj) {
          businessObject.$attrs[i] = obj[i];
        }
        const shape = elementFactory.createShape({
          type: 'bpmn:UserTask',
          businessObject,
        });
        create.start(event, shape);
      };
    }

    return {
      'create.start': {
        group: 'model',
        className: 'icon-custom start-box',
        title: translate(''),
        action: {
          dragstart: createStart('', 'start'),
          click: createStart('', 'start'),
        },
      },
      // 'create.flow': {
      //   group: 'model',
      //   className: 'icon-custom flow-box',
      //   title: translate(''),
      //   action: {
      //     dragstart: createFlow("1"),
      //     click: createFlow("1")
      //   }
      // },
      'create.endsucc': {
        group: 'model',
        className: 'icon-custom endsucc-box',
        title: translate(''),
        action: {
          dragstart: createEnd('', 'endsucc'),
          click: createEnd('', 'endsucc'),
        },
      },
      'create.endfail': {
        group: 'model',
        className: 'icon-custom endfail-box',
        title: translate(''),
        action: {
          dragstart: createEnd('', 'endfail'),
          click: createEnd('', 'endfail'),
        },
      },
      'create.task-task': {
        group: 'model',
        className: 'icon-custom task-box',
        title: translate(''),
        action: {
          dragstart: createTask('', 'task'),
          click: createTask('', 'task'),
        },
      },
      'create.auto-task': {
        group: 'model',
        className: 'icon-custom auto-box',
        title: translate(''),
        action: {
          dragstart: createSpecalTask(
            '',
            {
              'activiti:waitType': '0',
            },
            'auto'
          ),
          click: createSpecalTask(
            '',
            {
              'activiti:waitType': '0',
            },
            'auto'
          ),
        },
      },
      'create.approve-task': {
        group: 'model',
        className: 'icon-custom approve-box',
        title: translate(''),
        action: {
          dragstart: createTask('', 'approve'),
          click: createTask('', 'approve'),
        },
      },
      'create.sign-task': {
        group: 'model',
        className: 'icon-custom sign-box',
        title: translate(''),
        action: {
          dragstart: createTask('', 'sign'),
          click: createTask('', 'sign'),
        },
      },
      'create.subproc-task': {
        group: 'model',
        className: 'icon-custom subproc-box',
        title: translate(''),
        action: {
          dragstart: createSpecalTask(
            '',
            {
              'activiti:startPerson': '0',
              'activiti:waitType': '1',
            },
            'subproc'
          ),
          click: createSpecalTask(
            '',
            {
              'activiti:startPerson': '0',
              'activiti:waitType': '1',
            },
            'subproc'
          ),
        },
      },
    };
  }
}

CustomPalette.$inject = ['bpmnFactory', 'create', 'elementFactory', 'palette', 'translate'];
